<template>
  <div v-horizontal-scroll class="comp withdraw_type">
    <div class="withdraw_list">
      <div
        v-for="(item, inx) in list"
        :key="inx"
        class="withdraw_list_item cursor_point"
        :class="{ withdraw_list_active: item.value == value }"
        @click="onChange(item.value, item)"
      >
        {{ item.label }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    list: {
      type: Array,
      default() {
        return []
      }
    },
    value: {
      type: String,
      default: ''
    }
  },
  data() {
    return {}
  },
  methods: {
    onChange(value, item) {
      this.$emit('change', value, item)
    }
  }
}
</script>

<style scoped lang="scss">
@import '@style/components/game-container.modules.scss';

.comp.withdraw_type {
  width: 100%;
  overflow-x: auto;
  overflow-y: hidden;
  &::-webkit-scrollbar {
    display: block !important;
  }
  .withdraw_list {
    min-width: 100%;
    @include setBox(row, center, center);
    gap: 0 16px;
    .withdraw_list_item {
      height: $deposit_type_item_height;
      text-align: center;
      line-height: $deposit_type_item_line_height;
      background: $deposit_type_tab_bg;
      border-radius: $deposit_type_item_border_radius;
      white-space: nowrap;
      font-size: $deposit_type_item_font_size;
      font-weight: $deposit_type_item_font_weight;
      padding: $deposit_type_item_padding;
      &.withdraw_list_active {
        background: $deposit_type_tab_active_bg;
      }
    }
  }
}
</style>
