<!--
    当前版本不对card_type进行展示和提供操作:已经和客户端比对过
-->
<template>
  <div class="comp withdraw_fields">
    <div v-for="item in fieldList" :key="item.key" class="field_line">
      <!-- 标题 -->
      <div class="field_line_title">
        <div class="text">{{ $t(item.label) }}</div>
        <div v-if="item.required" class="color_red">*</div>
      </div>
      <!-- 输入框 -->
      <template v-if="item.type == 'input'">
        <div class="field_line_box">
          <input
            v-model="item.value"
            class="field_input input_radius"
            type="text"
            :placeholder="item.placeholder ? $t(item.placeholder) : ''"
            :disabled="item.disabled || false"
            @blur="onBlur(item)"
          />
        </div>
      </template>
      <!-- 单选框 -->
      <template v-if="item.type == 'radio'">
        <div class="field_line_box">
          <v-radio-group
            v-model="item.value"
            class="field_line_radio"
            :disabled="item.disabled || false"
            hide-details
            row
          >
            <v-radio
              v-for="ridioItem in item.radioList"
              :key="ridioItem.value"
              :value="ridioItem.value"
              :ripple="false"
              color="t_primary_dark_color"
            >
              <template v-slot:label>
                <div class="field_line_radio_title">{{ $t(ridioItem.label) }}</div>
              </template>
            </v-radio>
          </v-radio-group>
        </div>
        <template v-if="item.radioTempData">
          <withdraw-fields
            :list="item.radioTempData[item.value]"
            :user-wallet="userWallet"
            :bank-list="bankList"
            :withdrawal-type="withdrawalType"
          />
        </template>
      </template>
      <!-- 下拉框 -->
      <template v-if="item.type == 'select'">
        <div class="field_line_box">
          <vue-select
            v-model="item.value"
            :options="item.selectList"
            :placeholder="$t(item.placeholder)"
            :padding="commonStyle(!item.value ? 'withdraw_select_no_data_padding' : 'withdraw_select_data_padding')"
            :color="commonStyle('withdraw_select_text_color')"
            :background-color="commonStyle('withdraw_select_bg')"
            :font-size="commonStyle('withdraw_select_size')"
            :border="commonStyle('withdraw_select_border')"
            :border-radius="commonStyle('withdraw_select_radius')"
            :icon-color="commonStyle('withdraw_select_icon_color')"
            :icon-size="commonStyle('withdraw_select_icon_size')"
            :menu-border-radius="commonStyle('withdraw_select_menu_radius')"
            :menu-padding="commonStyle('withdraw_select_menu_padding')"
            :memu-bg="commonStyle('withdraw_select_memu_bg')"
            :active-radius="commonStyle('withdraw_select_active_radius')"
            :active-color="commonStyle('withdraw_select_active_color')"
            :active-bg="commonStyle('withdraw_select_active_bg')"
            :active-font-size="commonStyle('withdraw_select_active_size')"
            :active-item-padding="commonStyle('withdraw_select_active_padding')"
            :offset-y="commonStyle('withdraw_select_offset_y')"
            :offset-x="commonStyle('withdraw_select_offset_x')"
            :img-width="commonStyle('withdraw_select_img_width')"
            :img-height="commonStyle('withdraw_select_img_height')"
            :img-radius="commonStyle('withdraw_select_img_radius')"
            :input-height="commonStyle('withdraw_select_input_height')"
            :input-weight="commonStyle('withdraw_select_input_weight')"
            :disabled="item.disabled || false"
          />
        </div>
      </template>
      <!-- 多选框 -->
      <!-- <template v-if="item.type == 'checkbox'">
        <div class="field_line_box">
          <check-box v-for="check in item.checkboxList" :key="check.value" v-model="check.value">
            <div class="field_check_text">{{ $t(check.label) }}</div>
          </check-box>
        </div>
      </template> -->
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { deepClone, createCPF, againstCPF } from '@/utils/helper.js'
import { blindUserWallet, bindBankAccount } from '@/api/modules/config.js'
import VueSelect from '@/components/ui/vue-select'
// import CheckBox from '@/components/ui/CheckBox'
// import UserSelect from '@/components/ui/user-select'
import _common from '@style/components/game-container.modules.scss'

export default {
  name: 'WithdrawFields',
  components: {
    VueSelect
    // CheckBox
    // UserSelect
  },
  props: {
    // 表单列表
    list: {
      type: Array,
      default: () => []
    },
    // 用户钱包数据
    userWallet: {
      type: Object,
      default: () => {}
    },
    // 国家银行列表
    bankList: {
      type: Array,
      default: () => []
    },
    // 提现类型 0:银行提现 1:USDT提现
    withdrawalType: {
      type: String,
      default: '0'
    }
  },
  data() {
    return {
      fieldList: []
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'languageType', 'systemConfig']),
    ...mapState('source', ['phonePlaces'])
  },
  watch: {
    list: {
      handler() {
        this.fieldList = this.list
      },
      deep: true,
      immediate: true
    }
  },
  methods: {
    commonStyle: k => _common[k],
    // 输入框失去焦点时
    onBlur(item = {}) {
      // 是否需要格式化显示(cpf)
      if (item.format == 'cpf') item.value = createCPF(item.value)
    },
    /**
     * 前端校验
     * 增加 isCheck 字段，判断校验是否通过
     */
    checkFormatDate() {
      let newList = []
      // 递归方法，校验内层数据
      const recursionCheck = (list = []) => {
        return list.map(item => {
          // cpf 显示格式解除
          const value = item.format == 'cpf' ? againstCPF(item.value) : item.value
          const isCheck = item.rules(value)

          let { radioTempData, ...rest } = item
          let newItem = { ...rest, isCheck: item.required ? isCheck : true }
          newList.push(newItem)

          if (radioTempData) {
            const withinList = radioTempData[value]
            if (withinList && Array.isArray(withinList) && withinList.length > 0) {
              radioTempData[value] = recursionCheck(withinList)
              newItem.radioTempData = radioTempData
            }
          }

          return newItem
        })
      }
      this.fieldList = recursionCheck(this.fieldList)

      // 校验错误
      const errorList = newList.filter(item => !item.isCheck).map(m => this.$t(m.rulesText))
      // 校验不通过
      if (errorList.length > 0) {
        this.$pop.message().show({ content: errorList[0] })
        return false
      }

      return newList
    },
    /**
     * 服务端校验 -> 普通提现
     * v_position 版本国家 0中国 1印度（默认） 2尼日利亚
     * withdraw_switch 银行提现界面开关 1开启 0关闭
     * withdrawalType 0:银行提现 1:USDT提现
     * @param par 表单数据
     */
    async checkWithdrawal(par = {}) {
      const { v_position = 1, withdraw_switch = 0 } = this.userWallet || {}
      const { bank, account, name, ...restPar } = par

      // 银行提现开启 && 当前tab是是银行提现
      if (withdraw_switch == 1 && this.withdrawalType == '0') {
        let params = {
          token: this.user.token,
          language: this.languageType,
          type: v_position,
          ...restPar
        }
        // 对应的银行卡
        const bankItem = this.bankList.find(v => v.bank_code == bank)
        if (bankItem) {
          params.bank_name = bankItem.bank_name
          params.bank_code = bankItem.bank_code
        }
        // 银行账户
        if (account) params.bank_account = account
        // 持卡人
        if (name) params.cardholder_name = name

        const res = await bindBankAccount(params).catch(e => e)
        if (res.code != 0) {
          this.$pop.message().show({ content: res && res.msg })
          return false
        }

        const { tel = '', ...rest } = params
        this.$store.commit('base/SET_USER', { ...this.user, ...rest, cardholder_tel: tel })
        return true
      } else {
        return true
      }
    },
    /**
     * 服务端校验 -> usdt提现额外校验钱包地址
     * usdt_switch USDT提现界面开关 1开启 0关闭
     * withdrawalType 0:银行提现 1:USDT提现
     * @param par 表单数据
     */
    async checkUserWallet(par = {}) {
      const { usdt_switch = 0 } = this.userWallet || {}

      // usdt开启 && 当前tab是usdt提现 && 输入的钱包地址 != 当前用户钱包地址
      if (usdt_switch == 1 && this.withdrawalType == '1' && par.user_wallet != this.user.user_wallet) {
        const params = {
          token: this.user.token,
          wallet: par.user_wallet,
          language: this.languageType
        }
        const res = await blindUserWallet(params).catch(e => e)
        if (res.code != 0) {
          this.$pop.message().show({ content: res && res.msg })
          return false
        }
        return true
      } else {
        return true
      }
    },
    // 获取表单数据，验证校验
    async getFormatDate() {
      // 前端校验
      const webCheckList = this.checkFormatDate()
      if (!webCheckList) return false

      // 前端校验通过返回参数
      let params = {}
      webCheckList.forEach(item => {
        let val = item.value || ''
        // cpf 显示格式解除
        if (item.format == 'cpf') val = againstCPF(item.value)

        params[item.key] = val
      })

      // 普通提现服务端校验
      const checkState = await this.checkWithdrawal(params)
      if (!checkState) return false

      // usdt提现额外校验钱包地址
      const wallectState = await this.checkUserWallet(params)
      if (!wallectState) return false

      return true
    },
    // 表单重置
    reset() {
      this.fieldList.forEach(item => {
        item.value = ''
      })
    }
  }
}
</script>

<style scoped lang="scss">
@import '@style/components/game-container.modules.scss';

.comp.withdraw_fields {
  width: 100%;
  .field_line {
    width: 100%;
    margin: 18px 0 0 0;
    .field_line_title {
      width: 100%;
      @include setBox(row, flex-start, flex-start);
      gap: 0 4px;
      .text {
        font-size: 14px;
        font-weight: 600;
        color: $white;
        line-height: 1.2;
      }
      .color_red {
        font-size: 14px;
        font-weight: 400;
        color: $red_drop;
        line-height: 1.2;
      }
    }
    .field_line_box {
      width: 100%;
      margin: 12px 0 0 0;
      .field_input {
        width: 100%;
        height: 40px;
        padding: 0 12px;
        background: $input_bg;
        border: 1px solid $border_color;
        font-size: 14px;
        font-weight: 600;
        color: $white;
        line-height: 1.2;
        &:focus-visible {
          outline: none;
        }
        &::placeholder {
          color: $text_color_1;
        }
      }
      .field_line_radio {
        width: 100%;
        margin: 0 !important;
        padding: 0 !important;
        &::v-deep {
          .v-input__slot {
            min-height: auto !important;
          }
          .field_line_radio_title {
            font-size: 14px;
            font-weight: 600;
            color: $text_color_3;
            line-height: 1;
          }
        }
      }
    }
  }
}
</style>
