<template>
  <div class="comp withdraw_detail">
    <div class="withdraw_detail_info">
      <template v-if="withdrawTypeList.length">
        <!-- 提现类型 -->
        <template v-if="isWithdrawType">
          <withdraw-type :list="withdrawTypeList" :value="withdrawalType" @change="onChangeType" />
        </template>
        <!-- 提现标题和钱包入口 -->
        <div class="withdraw_detail_info_title">
          <div class="info_title_text text_1">{{ $t('UI_Deposit_money_msg') }}</div>
          <div class="info_title_close">
            <icon-font
              name="icon-Icon_information"
              class="cursor_point"
              dark="t_text_color_2"
              size="18"
              @click="onWalletShow"
            />
          </div>
        </div>
        <!-- 提现输入框 -->
        <div class="withdraw_detail_info_line">
          <withdraw-detail-amount v-model="withdrawalAmount" :max-amount="maxAmount" />
        </div>
        <div class="withdra_detail_usdt_proportion">
          <!-- usdt汇率 -->
          <template v-if="withdrawalType == '1'">
            <span class="usdt_rate_text">{{ usdtRate }}</span>
          </template>
          <!-- 提现手续费显示 -->
          <span class="withdrawal_fee_text">
            {{ usdtFeeText }}
          </span>
        </div>
        <!-- usdt 提现总计 -->
        <template v-if="withdrawalType == '1' && usdtTotal">
          <div class="withdra_detail_usdt_total">{{ usdtTotalText }}</div>
        </template>
        <!-- 提现表单 -->
        <div class="withdraw_field_box_form">
          <withdraw-fields
            ref="withdrawFieldRef"
            :list="fieldList"
            :user-wallet="userWallet"
            :bank-list="bankList"
            :withdrawal-type="withdrawalType"
          />
        </div>
        <!-- 提现免责说明 -->
        <div class="withdraw_notes_big_text">
          {{ $t('UI.cashout_1_bank_14') }}
        </div>
        <!-- 提现记录按钮 -->
        <div class="withdraw_notes_record">
          <span id="withdrawRecordId" class="records_link_text" @click="recordState = true">
            {{ $t('UI_Deposit_Withdraw_Notes') }}
          </span>
        </div>
      </template>
    </div>
    <div class="withdraw_detail_btn">
      <div v-zoom class="btn btn_radius t_primary_dark_color" @click="payAction">
        {{ $t('UI_btn_Withdraw') }}
      </div>
    </div>

    <!-- 客服 -->
    <kefu-inlet class="withdraw_kefu_inlet" />
    <!-- 提现钱包 -->
    <withdraw-carteira ref="walletRef" />
    <!-- 提现记录 -->
    <custom-dialog v-model="recordState" :full="true">
      <div :style="{ width: isApp ? '100%' : '650px' }">
        <recharge-history :tab="2" @close="recordState = false" />
      </div>
    </custom-dialog>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { channel } from '@/api/setting.js'
import { spreadWay } from '@/api/modules/agent.js'
import { bankWithdrawalTemp, usdtTemp } from './withdrawConfig.js'
import { getNigeriaConfig, getUserWallet } from '@/api/modules/config.js'
import { lodash, isFormatMillageAndNumber } from '@/utils/helper.js'
import { tx, agentTx, getInviteUserTx } from '@/api/modules/config.js'
import WithdrawType from './ui/withdraw-type.vue'
import WithdrawCarteira from './ui/withdraw-carteira.vue'
import WithdrawDetailAmount from './ui/withdraw-detail-amount.vue'
import WithdrawFields from './ui/withdraw-fields.vue'
import RechargeHistory from '@/components/ui/withdrawal-recharge-history'
import KefuInlet from '@/components/ui/kefu-inlet'
import PublishSubscribe from 'publish-subscribe-js'

export default {
  components: {
    WithdrawType,
    WithdrawCarteira,
    WithdrawDetailAmount,
    WithdrawFields,
    RechargeHistory,
    KefuInlet
  },
  data() {
    return {
      // 提现类型 0:银行提现 1:USDT提现
      withdrawalType: '0',
      // 国家银行列表
      bankList: [],
      // 用户钱包数据
      userWallet: {},
      // 输入的提现金额
      withdrawalAmount: '',
      // 代理佣金
      agentCommission: 0,
      // 提现记录弹窗显隐
      recordState: false,
      // 提现防抖
      isShake: false
    }
  },
  computed: {
    ...mapState('layout', ['containerBox']),
    ...mapState('switchState', ['currencyShow']),
    ...mapState('base', ['isApp', 'user', 'systemConfig', 'languageType']),
    ...mapState('modal', ['withdrawEntrance', 'updateWithdrawType']),
    ...mapState('source', ['withdrawShare', 'activityInvite']),
    // 货币符号显示
    currencySymbol() {
      const { currency_symbol = '' } = this.systemConfig
      return this.currencyShow ? currency_symbol : ''
    },
    /**
     * 提现类型切换按钮是否显示
     * withdraw_switch 银行提现界面开关 1开启 0关闭
     * usdt_switch USDT提现界面开关 1开启 0关闭
     */
    isWithdrawType() {
      const { withdraw_switch = '0', usdt_switch = '0' } = this.userWallet
      if (withdraw_switch == 1 && usdt_switch == 1) return true

      return false
    },
    /**
     * 提现类型列表
     * withdraw_switch 银行提现界面开关 1开启 0关闭
     * usdt_switch USDT提现界面开关 1开启 0关闭
     */
    withdrawTypeList() {
      const { withdraw_switch = '0', usdt_switch = '0' } = this.userWallet
      return [
        {
          label: this.$t('UI_Cash_USDT_Bankout'),
          value: '0',
          hidden: withdraw_switch == 1
        },
        {
          label: this.$t('UI_Cash_USDT_USDTout'),
          value: '1',
          hidden: usdt_switch == 1
        }
      ].filter(f => f.hidden)
    },
    /**
     * 提取最大金额
     * withdraw_gold 提现金币
     */
    maxAmount() {
      const { withdraw_gold = 0 } = this.userWallet || {}
      const { activity_user_task = {} } = this.activityInvite || {}
      const { progress_amount = 0 } = activity_user_task || {}

      if (this.withdrawEntrance == 0) return withdraw_gold
      // 推广中的提款功能进入
      if (this.withdrawEntrance == 1) return this.agentCommission
      // 活动邀请进入
      if (this.withdrawEntrance == 2) return progress_amount

      return 0
    },
    // usdt 汇率显示【rate: USDT汇率配置(每天变动)】
    usdtRate() {
      const { rate = '' } = this.userWallet
      if (this.withdrawalType == '1') return `1 USDT ≈ ${this.currencySymbol}${rate}`

      return ''
    },
    /**
     * 提现手续费
     * agent_percent 全民代提现手续费率
     * percent 普通提现手续费率
     * agent_exchange_invite_amount 邀请活动提现手续费率
     * ext_amount 普通提现额外手续费
     * min_amount 普通提现最小金额
     * usdt_percent USDT提现手续费率
     * agent_usdt_percent 全民代提现手续费率
     * agent_usdt_exchange_invite_amount 邀请活动提现手续费率
     * usdt_ext_amount USDT提现额外手续费
     * usdt_min_amount USDT提现最小金额
     */
    usdtFee() {
      const {
        agent_percent = 0,
        percent = 0,
        agent_exchange_invite_amount = 0,
        ext_amount = 0,
        min_amount = 0,
        usdt_percent = 0,
        agent_usdt_percent = 0,
        agent_usdt_exchange_invite_amount = 0,
        usdt_ext_amount = 0,
        usdt_min_amount = 0
      } = this.userWallet
      if (!Number(this.withdrawalAmount) || isNaN(Number(this.withdrawalAmount))) return 0

      // 手续费具体的值
      let rateNum = 0

      // 银行提现
      if (this.withdrawalType == '0') {
        // 普通手续费
        if (this.withdrawEntrance == 0) rateNum = parseFloat(this.withdrawalAmount * (percent / 100))
        // 全民代手续费
        if (this.withdrawEntrance == 1) {
          let money = parseFloat(this.withdrawalAmount * (agent_percent / 100)) + parseFloat(ext_amount)
          rateNum = money > parseFloat(min_amount) ? money : min_amount
        }
        // 邀请活动手续费
        if (this.withdrawEntrance == 2) {
          rateNum = parseFloat(this.withdrawalAmount * (agent_exchange_invite_amount / 100))
        }
      }
      // usdt 提现
      if (this.withdrawalType == '1') {
        // 普通手续费
        if (this.withdrawEntrance == 0) rateNum = parseFloat(this.withdrawalAmount * (usdt_percent / 100))
        // 全民代手续费
        if (this.withdrawEntrance == 1) {
          let money = parseFloat(this.withdrawalAmount * (agent_usdt_percent / 100)) + parseFloat(usdt_ext_amount)
          rateNum = money > parseFloat(usdt_min_amount) ? money : min_amount
        }
        // 邀请活动手续费
        if (this.withdrawEntrance == 2) {
          rateNum = parseFloat(this.withdrawalAmount * (agent_usdt_exchange_invite_amount / 100))
        }
      }

      return rateNum
    },
    // 提现手续费显示
    usdtFeeText() {
      const text = this.$t('UI_Commossion')
      const money = this.$u.twoDigit(Number(this.usdtFee))

      return `${text}${this.currencySymbol}${this.thousandths(money, true)}`
    },
    // usdt 总计【rate: USDT汇率配置(每天变动)】
    usdtTotal() {
      const { rate = 0 } = this.userWallet

      return ((Number(this.withdrawalAmount) - Number(this.usdtFee)) / rate).toFixed(2)
    },
    // usdt 提现总计显示
    usdtTotalText() {
      const text = this.$t('UI_Total')
      const money = this.$u.twoDigit(Number(this.usdtTotal))

      return `${text}: ${this.currencySymbol}${this.thousandths(money, true)} USTD`
    },
    // 提交表单列表模板构造
    fieldList() {
      let list = []

      // 银行提现
      if (this.userWallet.withdraw_switch == 1 && this.withdrawalType == '0') {
        list = bankWithdrawalTemp(this.userWallet, this.bankList)
      }
      // usdt提现
      if (this.userWallet.usdt_switch == 1 && this.withdrawalType == '1') {
        list = usdtTemp()
      }

      return list
    }
  },
  watch: {
    // 提现功能回调刷新,更新代理佣金
    updateWithdrawType() {
      this.spreadWayFun()
    }
  },
  created() {
    // 活动邀请进入
    if (this.withdrawEntrance == 2) {
      const { activity_user_task = {} } = this.activityInvite || {}
      const { progress_amount = '' } = activity_user_task
      this.withdrawalAmount = progress_amount
    }

    this.getUserWalletInfo()
    this.getBankList()
  },
  methods: {
    // 千分比转换
    thousandths(key, isState) {
      const { thousand_mark = 0 } = this.systemConfig
      return isFormatMillageAndNumber(key, isState, thousand_mark)
    },
    // 获取国家银行列表
    async getBankList() {
      // 判断是否获取国家银行列表
      const { pay_userinfo_fields = {} } = this.systemConfig
      const { prepay = {} } = pay_userinfo_fields
      if (prepay.bank != 1) return

      const params = {
        token: this.user?.token,
        language: this.languageType
      }
      const res = await getNigeriaConfig(params).catch(e => e)
      if (res.code != 0) return

      const resData = res.data && Array.isArray(res.data) ? res.data : []
      this.bankList = resData
    },
    /**
     * 获取用户钱包信息
     * withdraw_switch 银行提现界面开关 1开启 0关闭
     * usdt_switch USDT提现界面开关 1开启 0关闭
     */
    async getUserWalletInfo() {
      const params = {
        token: this.user?.token,
        language: this.languageType,
        type: this.withdrawEntrance == 0 ? 1 : 2
      }
      const res = await getUserWallet(params).catch(e => e)
      if (res.code != 0) return

      const resData = res.data && this.$u.lodash.isObject(res.data) ? res.data : {}
      this.userWallet = resData

      // 设置提现类型
      const { withdraw_switch = 0, usdt_switch = 0, agent_commission = 0 } = this.userWallet
      this.withdrawalType = withdraw_switch == 1 ? '0' : usdt_switch == 1 ? '1' : '0'
      // 设置默认代理佣金
      this.agentCommission = agent_commission
      /**
       * 获取代理佣金
       * 0：等顶部导航栏进入，1：从推广中的提款功能进入 2: 活动邀请进入
       */
      if (this.withdrawEntrance == 1 || this.withdrawEntrance == 2) this.spreadWayFun()
    },
    // 提现类型切换
    onChangeType(value = '0') {
      this.withdrawalType = value

      // 活动邀请进入
      if (this.withdrawEntrance == 2) {
        const { activity_user_task = {} } = this.activityInvite || {}
        const { progress_amount = '' } = activity_user_task
        this.withdrawalAmount = progress_amount
      } else {
        this.withdrawalAmount = ''
      }
    },
    // 打开提现钱包
    onWalletShow() {
      if (this.$refs['walletRef']) this.$refs['walletRef'].show()
    },
    // 获取代理佣金
    async spreadWayFun() {
      const res = await spreadWay({ token: this.user.token, language: this.languageType }).catch(e => e)
      const resData = res.data && lodash.isObject(res.data) ? res.data : {}

      this.agentCommission = resData.agent_commission || 0
    },
    /**
     * 点击提现
     * withdraw_time_limit	非提现时间配置 0表示在提现时间 1表示不在
     * withdraw_time_text		非提现时间文本配置
     */
    async payAction() {
      if (!lodash.isMoney(this.withdrawalAmount)) {
        return this.$pop.message().show({ content: this.$t('UI_Deposit_money_msg') })
      }

      this.$nextTick(async () => {
        const withdrawFieldRef = this.$refs['withdrawFieldRef']
        if (!withdrawFieldRef) return
        if (this.isShake) return

        this.isShake = true
        // 获取输入的参数, 表单校验
        const isfield = await withdrawFieldRef.getFormatDate()
        if (!isfield) return (this.isShake = false)

        // 判断是否在提现时间
        const _this = this
        if (this.userWallet.withdraw_time_limit == 1) {
          this.$pop
            .confirm({
              content: this.userWallet?.withdraw_time_text || '',
              confirmText: this.$t('General.General_1'),
              cancelText: this.$t('General.General_2'),
              success() {
                _this.onWithdrawal()
              }
            })
            .show()
        } else {
          this.onWithdrawal()
        }
      })
    },
    // 提现
    async onWithdrawal() {
      // 判断是否在体育赛事页面，下分
      if (this.$route.name == 'sportView') {
        const par = { token: this.user.token, language: this.languageType }
        await this.$store.dispatch('sport/lowerDivision', par).catch(e => e)
      }

      // 普通提现
      if (this.withdrawEntrance == 0) this.onBankWithdrawal()
      // 全民代提现
      if (this.withdrawEntrance == 1) this.onAgentWithdrawal()
      // 活动邀请提现
      if (this.withdrawEntrance == 2) this.onInviteWithdrawal()
    },
    // 普通提现
    async onBankWithdrawal() {
      // 参数
      const params = {
        token: this.user.token,
        channel: channel,
        amount: this.withdrawalAmount,
        is_usdt: this.withdrawalType,
        language: this.languageType
      }
      const res = await tx(params).catch(e => e)
      if (res.code != 0) {
        this.isShake = false
        this.$pop.message().show({ content: res && res.msg })
        return
      }

      const resData = res.data && this.$u.lodash.isObject(res.data) ? res.data : {}
      this.withdrawalAmount = ''
      // 插入弹窗序列
      PublishSubscribe.publish(this.$store.state?.dialog?.DUALOG_MANAGER_KEY, {
        key: 'deposit',
        seat: 'first',
        params: {
          title: this.$t('General.General_9'),
          value: resData.msg || '',
          isIcon: false
        }
      })

      this.isShake = false
      this.getUserWalletInfo()
    },
    // 全民代提现
    async onAgentWithdrawal() {
      // 参数
      const params = {
        token: this.user.token,
        channel: channel,
        amount: this.withdrawalAmount,
        is_usdt: this.withdrawalType,
        language: this.languageType
      }
      const res = await agentTx(params).catch(e => e)
      if (res.code != 0) {
        this.isShake = false
        this.$pop.message().show({ content: res && res.msg })
        return
      }

      const resData = res.data && this.$u.lodash.isObject(res.data) ? res.data : {}
      this.withdrawalAmount = ''
      // 插入弹窗序列
      PublishSubscribe.publish(this.$store.state?.dialog?.DUALOG_MANAGER_KEY, {
        key: 'deposit',
        seat: 'first',
        params: {
          title: this.$t('General.General_9'),
          value: resData.msg || '',
          isIcon: false
        }
      })

      this.isShake = false
      this.getUserWalletInfo()
      this.$store.commit('source/SET_WITHDRAW_SHARE', !this.withdrawShare)
    },
    // 活动邀请提现
    async onInviteWithdrawal() {
      // 参数
      const params = {
        id: this.activityInvite?.activity_user_task?.task_id,
        token: this.user.token,
        channel: channel,
        amount: this.withdrawalAmount,
        is_usdt: this.withdrawalType,
        language: this.languageType
      }
      const res = await getInviteUserTx(params).catch(e => e)
      if (res.code != 0) {
        this.isShake = false
        this.$pop.message().show({ content: res && res.msg })
        return
      }

      const resData = res.data && this.$u.lodash.isObject(res.data) ? res.data : {}
      this.withdrawalAmount = ''
      // 插入弹窗序列
      PublishSubscribe.publish(this.$store.state?.dialog?.DUALOG_MANAGER_KEY, {
        key: 'deposit',
        seat: 'first',
        params: {
          title: this.$t('General.General_9'),
          value: resData.msg || '',
          isIcon: false
        }
      })

      this.isShake = false
      this.getUserWalletInfo()
      // 获取活动转盘配置信息
      const par = {
        token: this.user.token,
        language: this.languageType
      }
      this.$store.dispatch('source/getActivityConfigFn', par).catch(e => e)
    }
  }
}
</script>

<style lang="scss" scoped>
.comp.withdraw_detail {
  width: 100%;
  height: 100%;
  position: relative;
  @include setBox(column, center, center);
  .withdraw_detail_info {
    flex-grow: 1;
    flex-shrink: 1;
    width: 100%;
    padding: 32px 16px 48px 16px;
    overflow-y: auto;
    &::-webkit-scrollbar {
      display: block !important;
      width: 4px;
    }
    .withdraw_detail_info_title {
      width: 100%;
      margin: 26px 0px 0px 0px;
      @include setBox(row, center, center);
      gap: 0 10px;
      .info_title_text {
        flex-shrink: 1;
        flex-grow: 1;
        font-size: 16px;
        font-weight: 400;
        color: $white;
        line-height: 1.3;
      }
      .info_title_close {
        flex-shrink: 0;
        flex-grow: 0;
        font-size: 0;
      }
    }
    .withdraw_detail_info_line {
      width: 100%;
      margin: 16px 0 0 0;
    }
    .withdra_detail_usdt_proportion {
      width: 100%;
      margin: 10px 0 0 0;
      @include setBox(row, space-between, center, wrap);
      .usdt_rate_text,
      .withdrawal_fee_text {
        font-size: 12px;
        font-weight: 400;
        color: $text_color_4;
        line-height: 1;
      }
    }
    .withdra_detail_usdt_total {
      width: 100%;
      margin: 20px 0 0 0;
      font-size: 12px;
      font-weight: 400;
      color: $text_color_4;
      line-height: 1;
    }
    .withdraw_field_box_form {
      width: 100%;
      margin: 20px 0 0 0;
      border-top: 1px solid $border_hr_color;
    }
    .withdraw_notes_big_text {
      width: 100%;
      margin: 20px 0 0 0;
      font-size: 14px;
      font-weight: 600;
      color: $text_color_4;
      line-height: 1.2;
      word-break: break-word;
    }
    .withdraw_notes_record {
      width: 100%;
      margin: 16px 0 0 0;
      @include setBox(row, flex-start, center);
      .records_link_text {
        font-size: 16px;
        font-weight: 600;
        color: $primary_dark_color;
        line-height: 1;
        text-decoration: underline;
        cursor: pointer;
      }
    }
  }
  .withdraw_kefu_inlet {
    position: absolute;
    bottom: 120px;
    right: 10px;
  }
  .withdraw_detail_btn {
    flex-grow: 0;
    flex-shrink: 0;
    width: 100%;
    padding: 14px 16px;
    background: $page_dialog_bg_2;
    border-top: 1px solid $border_hr_color;
    .btn {
      width: 100%;
      height: 44px;
      font-size: 14px;
      font-weight: 600;
      line-height: 1;
      @include setBox(row, center, center);
    }
  }
}

//  pc 端兼容样式
@include breakpoint-only-up() {
  .comp.withdraw_detail {
    .withdraw_detail_info {
      height: 600px;
      padding: 32px 40px 60px 40px;
    }
  }
}
</style>
