<template>
  <custom-dialog v-model="visible" :full="true">
    <div class="withdraw_carteira_box">
      <div class="carteira_box_top">
        <div class="carteira_box_top_title text_1">
          {{ $t('UI_Withdrawal_Instructions') }}
        </div>
        <icon-font
          class="carteira_box_top_colse cursor_point"
          name="icon-Icon_Cloose"
          dark="t_text_color_2"
          :size="commonStyle('carteira_box_top_colse_size')"
          @click="onClose"
        />
      </div>
      <div class="carteira_box_content">
        <div class="carteira_box_withdrawal">
          <withdrawal
            :title="$t('UI_withdrawal')"
            sub-title
            :accountBg="loadIcon('carteira_bg_qianbao')"
            :retiradaIcon="loadIcon('carteira_qianbao')"
            :type="true"
            :value="userInfo"
          />
        </div>
        <div class="carteira_box_bottom" v-html="descrever"></div>
      </div>
    </div>
  </custom-dialog>
</template>

<script>
import { mapState } from 'vuex'
import { getUserInfoAndOrder, getInfoCache } from '@/api/modules/config.js'
import Withdrawal from '@views/mine/ui/withdrawal.vue'
import loadIcon from '@/assets/icons.js'
import _common from '@style/components/game-container.modules.scss'

export default {
  components: {
    Withdrawal
  },
  data() {
    return {
      visible: false,

      limit: 10,
      page: 1,
      pageType: 0,
      descrever: '', // 描述
      userInfo: {
        all_bet: 0,
        can_withdraw: 0,
        current_bet: 0,
        not_withdraw: 0
      }
    }
  },
  computed: {
    ...mapState('layout', ['containerBox']),
    ...mapState('base', ['isApp', 'user', 'languageType'])
  },
  methods: {
    loadIcon: key => loadIcon(key, false),
    commonStyle: k => _common[k],
    // 打开
    show() {
      this.visible = true
      this.getWalletInfo()
    },
    // 关闭
    onClose() {
      this.visible = false
    },
    // 获取钱包数据
    async getWalletInfo() {
      const params = {
        type: this.pageType,
        token: this.user.token,
        language: this.languageType,
        page: this.page,
        limit: this.limit
      }
      const par = {
        token: this.user.token,
        name: 'user_center_withdraw_desc',
        type: 0,
        language: this.languageType
      }

      this.$pop.loading().show()
      const [userRes, infoRes] = await Promise.all([
        getUserInfoAndOrder(params).catch(e => e),
        getInfoCache(par).catch(e => e)
      ])
      this.$pop.loading().hide()

      // 获取金额数据
      if (userRes && userRes.code == 0) this.userInfo = userRes?.data?.user_info || {}
      // 获取描述
      if (infoRes.code == 0) this.descrever = infoRes?.data?.value || ''
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/game-container.modules.scss';

.withdraw_carteira_box {
  width: $withdraw_carteira_box_border_width;
  height: $withdraw_carteira_box_border_height;
  background: $withdraw_carteira_bg;
  border-radius: $withdraw_carteira_box_border_radius;
  @include setBox(column, center, center);
  .carteira_box_top {
    flex-shrink: 0;
    flex-grow: 0;
    width: 100%;
    padding: 16px 32px;
    position: relative;
    @include setBox(row, center, center);
    .carteira_box_top_colse {
      position: absolute;
      right: 16px;
    }
    .carteira_box_top_title {
      font-size: $carteira_box_top_title_font_size;
      font-weight: $carteira_box_top_title_font_weight;
    }
  }
  .carteira_box_content {
    flex-shrink: 1;
    flex-grow: 1;
    width: 100%;
    padding: 0 16px 16px 16px;
    overflow: hidden;
    @include setBox(column, center, center);
    .carteira_box_withdrawal {
      flex-shrink: 0;
      flex-grow: 0;
    }
    .carteira_box_bottom {
      flex-shrink: 1;
      flex-grow: 1;
      width: 100%;
      overflow-y: auto;
      color: $withdraw_carteira_text_color;
      margin: $carteira_box_bottom_margin;
      white-space: pre-wrap;
      word-break: break-word;
      &::-webkit-scrollbar {
        display: block !important;
        width: 4px;
      }
    }
  }
}

//  pc 端兼容样式
@include breakpoint-only-up() {
  .withdraw_carteira_box {
    width: 650px;
    .carteira_box_content {
      .carteira_box_bottom {
        height: 300px;
      }
    }
  }
}
</style>
