<template>
  <div class="withdraw_detail_amount">
    <div class="withdraw_detail_amount_title">
      <span class="money_title_color">{{ $t('UI_MONEY_TITLE') }}</span>
      <span class="withdraw_detail_amount_money_color">
        {{ `${currencySymbol}${thousandths(maxAmount, true) || 0}` }}
      </span>
    </div>
    <div class="withdraw_detail_amount_field">
      <input
        class="withdraw_detail_amount_field_input input_radius"
        type="text"
        :value="amountText"
        :placeholder="$t('UI_Deposit_amountLabel')"
        :disabled="withdrawEntrance == 2"
        @input="onChangeInput"
        @keydown="keydownAmount"
      />
      <div class="withdraw_detail_amount_field_symbol">{{ currencySymbol }}</div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { isFormatMillageAndNumber } from '@/utils/helper.js'

export default {
  model: {
    prop: 'amount',
    event: 'change'
  },
  props: {
    // 输入的提取金额
    amount: {
      type: [Number, String],
      default: ''
    },
    // 提取金额最大值
    maxAmount: {
      type: [Number, String],
      default: 0
    }
  },
  data() {
    return {}
  },
  computed: {
    ...mapState('base', ['isApp', 'systemConfig']),
    ...mapState('switchState', ['currencyShow']),
    ...mapState('modal', ['withdrawEntrance']),
    // 币种符号
    currencySymbol() {
      const { currency_symbol = '' } = this.systemConfig
      return this.currencyShow ? currency_symbol : ''
    },
    // 金额显示
    amountText() {
      return this.thousandths(this.amount, true)
    }
  },
  methods: {
    // 千分比转换
    thousandths(key, isState) {
      const { thousand_mark = 0 } = this.systemConfig
      return isFormatMillageAndNumber(key, isState, thousand_mark)
    },
    // 输入框变化
    onChangeInput(e) {
      let moneyVal = e.target.value
      moneyVal = moneyVal.replace(/[^0-9]/g, '')
      this.$emit('change', moneyVal)
    },
    // 监听输入框键盘输入事件
    keydownAmount(event) {
      // 允许输入数字和特殊键（退格、删除、方向键等）
      const allowedKeyCodes = [8, 9, 13, 27, 35, 36, 37, 38, 39, 40, 46]
      if (!(event.ctrlKey || event.metaKey || /[0-9]/.test(event.key) || allowedKeyCodes.includes(event.keyCode))) {
        event.preventDefault()
      }
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/game-container.modules.scss';

.withdraw_detail_amount {
  width: 100%;
  .withdraw_detail_amount_title {
    font-size: $withdraw_detail_amount_title_font_size;
    font-weight: $withdraw_detail_amount_title_font_weight;
    line-height: 1.2;
    .money_title_color {
      color: $withdraw_at_present_text_title_color;
    }
    .withdraw_detail_amount_money_color {
      color: $withdraw_at_present_text_color;
    }
  }
  .withdraw_detail_amount_field {
    width: 100%;
    margin: 12px 0 0 0;
    position: relative;
    .withdraw_detail_amount_field_input {
      width: 100%;
      height: 40px;
      padding: 0 12px 0 32px;
      background: $input_bg;
      border: 1px solid $border_color;
      font-size: 14px;
      font-weight: 600;
      color: $white;
      line-height: 1.2;
      &:focus-visible {
        outline: none;
      }
      &::placeholder {
        color: $text_color_1;
      }
    }
    .withdraw_detail_amount_field_symbol {
      position: absolute;
      left: 12px;
      top: 50%;
      transform: translateY(-50%);
      font-size: 14px;
      font-weight: 600;
      color: $text_color_1;
      line-height: 1.2;
    }
  }
}
</style>
